import React from 'react';
import './home-apps.scss';
import { useInternalJson } from '../../utils/hooks/use-internal-json';
import { Product } from '../product/product';

export const HomeApps = () => {
    const { list } = useInternalJson('data/apps/index.json', {});
    
    return (
        <div className="home-apps-container">
            { !!list && list.map((path) => (
                <Product key={ path } path={ `data/apps/${ path }` }/>
            )) }
        </div>
    )
};
