import React from 'react';

export const ProductBadgeGooglePlay = ({ productId }) => {
    const href = `https://play.google.com/store/apps/details?id=${ productId }&pcampaignid=pcampaignidMKT-Other-global-all-co-prtnr-py-PartBadge-Mar2515-1`;
    return (
        <a href={ href}>
            <img alt='Get it on Google Play' src='https://play.google.com/intl/en_us/badges/static/images/badges/en_badge_web_generic.png' className="google-badge"/>
        </a>
    )
};
