import { useWindowResize } from './use-window-resize';
import { getMobile } from '../getters/get-mobile';

const _MOBILE_MAX_WIDTH = 500;

/**
 * This hook retrieves responsive information.
 * For efficiency purposes, consider using this hook only once, and assign the result
 * to a ResponsiveContextProvider
 * @returns {import('../contexts/responsive-context').ResponsiveInfo} responsive info
 */
export const useMobile = () => {
    const { width, height } = useWindowResize();
    const mobile = getMobile();

    return {
        landscape: width > height,
        mobile: mobile || (width && width < _MOBILE_MAX_WIDTH),
        portrait: width < height,
        smallWidth: width < _MOBILE_MAX_WIDTH
    };
};
