import React from 'react';
import { Link } from 'react-router-dom';
import './privacy-list.scss';

export const PrivacyList = ({ list }) => (
    <div className="privacy-list-container">
        { !!list && list.map(({ path, name }) => (
            <Link key={ path } to={ `privacy/${ path }` }>{ name }</Link>
        )) }
    </div>
);
    
