import { useInternalFile } from './use-internal';

const markdownResponseCallback = (response) => response.text();
/**
 * Given a path, with or without .md extension, it will fetch and cache that information
 * @returns {string} string value of the markdown file
 */
export const useInternalMarkdown = (path, defaultValue = '') =>
    useInternalFile(path, defaultValue, '.md', markdownResponseCallback);