import React, { useState } from 'react';
import './home-contact.scss';
import { useInternalJson } from '../../utils/hooks/use-internal-json';

export const HomeContact = () => {
    const [ message, setMessage ] = useState('');
    const { mail, title, description } = useInternalJson('data/contact.json', { mail: '', title: '', description: ''});

    const handleChange = (event) => setMessage(event.target.value)
    const handleSubmit = () => window.location.href = `mailto:${ mail }?subject=web-support&body=${ message }`;

    return (
        <div className="home-contact-container">
            <form onSubmit={ handleSubmit } className="contact-panel">
                <h1>{ title }</h1>
                <p>{ description }</p>
                    <textarea placeholder="write your message here" value={ message } onChange={ handleChange }/>
                <div className="f-end">
                    <input type="submit" value="Send" />
                </div>
            </form>
        </div>
    );
};
