import React from 'react';
import { useParams } from 'react-router-dom';
import { PrivacyPolicy } from '../privacy-policy/privacy-policy';
import { PrivacyList } from '../privacy-list/privacy-list';
import { useInternalJson } from '../../utils/hooks/use-internal-json';
import './home-privacy.scss';

export const HomePrivacy = () => {
    const { privacyId } = useParams();
    const { list } = useInternalJson('data/privacy/index.json', { list: []});

    return (
        <div className="privacy-policy-container">
            <div className="privacy-policy-content">
                { !privacyId 
                    ? <PrivacyList list={ list } />
                    : <PrivacyPolicy pathname={ `data/privacy/${ privacyId }.md` } /> }
            </div>
        </div>
    )
};
