import React from 'react';
import { AsideNav } from '../aside-nav/aside-nav';
import './aside.scss';

export const Aside = ({ open, onClose }) => {
    const className = `aside-container${ !open ? ' aside-container--hidden' : '' }`;

    return (
        <div className={ className }>
            <AsideNav onSelect={ onClose }/>
        </div>
    );
}