import { useState, useEffect } from 'react';

const loadingInfoMap = new Map();
const storedInfoMap = new Map();

export const useInternalFile = (path, defaultValue = {}, defaultExtension = '', successCallback = (a) => a) => {
    const [ value, setValue ] = useState(defaultValue);
    useEffect(() => {
        const fullpath = !!path ? `${ path }${ path.endsWith(defaultExtension) ? '' : defaultExtension }` : '';
        
        // Given an invalid path, log some error information
        if (!fullpath) {
            console.error('incorrect use of useInternalFile');
        // Fetch information only once. So if this function gets called, first check if it already exists
        } else if (storedInfoMap.has(fullpath)) {
            console.log(`retrieving cached document "${ fullpath }"`);
            setValue(storedInfoMap.get(fullpath));
        // If information does not exists. Do a fetch unless its already being done
        } else if (!loadingInfoMap.get(fullpath)){
            loadingInfoMap.set(fullpath, true)
            fetch(fullpath)
                .then(successCallback)
                .then(response => {
                    setValue(response);
                    storedInfoMap.set(fullpath, response);
                })
                .catch(error => console.log(error))
                .finally(() => loadingInfoMap.set(fullpath, false));
        }
    }, [ path, defaultValue, defaultExtension, successCallback ]);

    return value;
};