import { createContext, useContext } from 'react';

/** An object containing information related to responsive context
 *  @typedef {Object} ResponsiveInfo
 *  @property {boolean} landscape True/false for wider than taller window visualization
 *  @property {boolean} mobile True/false for mobile rendering or very smal width
 *  @property {boolean} portrait True/false for taller than wider window visualization
 *  @property {boolean} smallWidth True/false for very small width screens
 */

const ResponsiveContext = createContext({
    landscape: false,
    mobile: false,
    portrait: false,
    smallWidth: false
});
const ResponsiveContextProvider = ResponsiveContext.Provider;
/**
 *  @returns {ResponsiveInfo}
 */
const useContextResponsive = () => useContext(ResponsiveContext);

export { ResponsiveContextProvider, useContextResponsive }; 
