import React, { useEffect, useState} from 'react';

export const ProductSlideshow = ({ screenshots }) => {
    const [ thumbnails, setThumbnails ] = useState([]);
    const [ currentSlide, setCurrentSlide ] = useState(0);
    const [ currentSlideStyle, setCurrentSlideStyle ] = useState({});

    useEffect(() => {
        setThumbnails(screenshots);
        setCurrentSlideStyle({
            backgroundImage: "url('screenshots/" + screenshots[ currentSlide ] + "')"
        })
    }, [ screenshots, currentSlide ])

    const previous = () => setCurrentSlide(prevState => prevState > 0 ? prevState - 1 : thumbnails.length - 1);
    const next = () => setCurrentSlide(prevState => prevState === thumbnails.length - 1 ? 0 : prevState + 1);

    return (
        <div className="product-slideshow">
            <div className="slideshow-holder">
                <section className="slide previous-slide">
                    <div className="slide-thumbnail"></div>
                </section>
                <section className="slide current-slide">
                    <div className="slide-thumbnail" style={ currentSlideStyle }></div>
                </section>
                <section className="slide next-slide">
                    <div className="slide-thumbnail"></div>
                </section>
            </div>
            <div className="slideshow-controller">
                <span onClick={ previous }>Previous</span>
                <span onClick={ next }>Next</span>
            </div>
        </div>
    );
}