import React, { useState } from 'react';
import { Switch, Route } from 'react-router-dom';
import { NotFound } from './components/not-found/not-found.jsx';
import { Header } from './components/header/header.jsx';
import { HomeGames } from './components/home-games/home-games.jsx';
import { HomeApps } from './components/home-apps/home-apps.jsx';
import { HomeAbout } from './components/home-about/home-about.jsx';
import { HomePrivacy } from './components/home-privacy/home-privacy.jsx';
import { Footer } from './components/footer/footer.jsx';
import { HomeContact } from './components/home-contact/home-contact.jsx';
import { ResponsiveContextProvider } from './utils/contexts/responsive-context';
import { useMobile } from './utils/hooks/use-mobile.js';
import { ScrollTopButton } from './components/scroll-top-button/scroll-top-button.jsx';
import { Aside } from './components/aside/aside.jsx';

export const App = () => {
  const [ asideOpen, setAsideOpen ] = useState(false);
  const responsiveInfo = useMobile();
  const { mobile } = responsiveInfo;

  const toggleAside = () => setAsideOpen((prevState) => !prevState);
  const closeAside = () => setAsideOpen(false);

  return (
    <ResponsiveContextProvider value={ { ...responsiveInfo } }>
      <div className="app-container" > 
        <Header onMenuClick={ toggleAside } />
        <div className="main-container">
          <Switch>
            <Route exact path="/apps" component={ HomeApps } />
            <Route exact path="/games" component={ HomeGames } />
            <Route exact path="/about" component={ HomeAbout } />
            <Route exact path="/contact" component={ HomeContact } />
            <Route exact path={ [ "/privacy", "/privacy/:privacyId" ] } component={ HomePrivacy } />
            <Route exact path={ [ "/", "/home" ] } component={ HomeGames } />
            <Route component={ NotFound } />
          </Switch>
        </div>
        <Footer />
        <ScrollTopButton />
        { mobile ? <Aside open={ asideOpen } onClose={ closeAside }/> : null }
      </div>
    </ResponsiveContextProvider>
  );
};
