import React, { useState, useEffect } from 'react';

const DISPLAY_STYLE = {};
const NO_DISPLAY_STYLE = { display: 'none' };
const VISIBLE_STYLE = { opacity: '1', transition: 'all 0.5s' };
const NO_VISIBLE_STYLE = { opacity: '0', transition: 'all 0.5s' };

export const AnimatedVisible = ({ visible, children }) => {
    const [ displayStyle, setDisplayStyle ] = useState(visible ? DISPLAY_STYLE : NO_DISPLAY_STYLE);
    const [ visibleStyle, setVisibleStyle ] = useState(visible ? VISIBLE_STYLE : NO_VISIBLE_STYLE);
    const [ displayTimeout, setDisplayTimeout ] = useState(null);
    const [ visibleTimeout, setVisibleTimeout ] = useState(null);

    const elementStyle = {
        ...displayStyle,
        ...visibleStyle
    };

    useEffect(() => {
        visibleTimeout && clearTimeout(visibleTimeout);
        displayTimeout && clearTimeout(displayTimeout);

        if (visible) {
            setVisibleStyle(NO_VISIBLE_STYLE);
            setDisplayStyle(DISPLAY_STYLE);
            setVisibleTimeout(setTimeout(() => setVisibleStyle(VISIBLE_STYLE), 500));
        } else {
            setVisibleStyle(NO_VISIBLE_STYLE);
            setDisplayTimeout(setTimeout(() => setDisplayStyle(NO_DISPLAY_STYLE), 500));
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [ visible ]);

    return (
        <div style={ elementStyle }>
            { children }
        </div>
    );
}