import React from 'react';
import { Link, useLocation } from 'react-router-dom';

const links = [
    ['/apps', 'Apps'],
    ['/games', 'Games'],
    ['/about', 'About us'],
    ['/contact', 'Contact'],
    ['/privacy', 'Privacy']
];

export const CustomNav = ({ className, onPostLink }) => {
    const { pathname } = useLocation();
    const handleClick = onPostLink ? onPostLink : () => {};

    return (
        <div className={ `${ className }-container` }>
            { links.map(([path, name]) => (
                <div key={ path } className={ `${ className }-container__item ${ pathname === path ? `${ className }-container__item--active`  : '' }` }>
                    <Link to={ path } onClick={ handleClick }>{ name }</Link>
                </div>
            )) }
        </div>
    )
};
