import React from 'react';
import './scroll-top-button.scss';
import { useScrollToTop } from '../../utils/hooks/use-scroll-to-top';
import { AnimatedVisible } from '../../utils/components/animated-visible';
import { IconArrowUp } from '../icons/icon-arrow-up';

export const ScrollTopButton = () => {
    const [ isScrollVisible, scrollToTop ] = useScrollToTop();

    return (
        <div className="scroll-top-button">
            <AnimatedVisible visible={ isScrollVisible }>
                <button onClick={ scrollToTop } >
                    <IconArrowUp />
                </button>
            </AnimatedVisible>
        </div>
    );
};
