import React, { useState } from 'react';

export const ProductScreenshot = ({ screenshots }) => {
    const [ link ] = screenshots; 
    const [ zoom, setZoom ] = useState(false)

    const handleZoomOut = () => setZoom(false);
    const handleZoomIn = () => setZoom(true);

    return (
        <div className="product-screenshot">
            <img src={ `screenshots/${ link }`} alt={ link } onClick={ handleZoomIn } />
            { !!zoom && (
                <div className="product-screenshot__modal" onClick={ handleZoomOut }>
                    <img src={ `screenshots/${ link }`} alt={ link } onClick={ handleZoomOut } />
                </div>
            ) }
        </div>
    );
};
