import React from 'react';
import './product-theme-red.scss';
import './product-theme-green.scss';
import './product-theme-blue.scss';
import './product-theme-yellow.scss';
import { useInternalJson } from '../../utils/hooks/use-internal-json';
import { ProductTitle } from './product-title';
import { ProductBadgeGooglePlay } from './product-badge-google-play';
import { ProductScreenshot } from './product-screenshot';
import { ProductDescription } from './product-description';
import { ProductSlideshow } from './product-slideshow';
import { useContextResponsive } from '../../utils/contexts/responsive-context';

const components = {
    'title': ProductTitle,
    'description': ProductDescription,
    'screenshot':  ProductScreenshot,
    'slideshow': ProductSlideshow,
    'badge-google': ProductBadgeGooglePlay
}

const getContentClassName = (type, vertical = false) => `${ type } ${ vertical ? `${ type }--portrait` : '' }`;

const getProductClassName = (data) => {
    const { theme } = data;

    return `product-container-${ theme || ''}`
}

/**
 * Check if content must flow vertically when needed
 * @param {import('../../utils/contexts/responsive-context').ResponsiveInfo} responsiveInfo 
 * @returns {Boolean} True/false
 */
const isContentVertical = ({ landscape, mobile, portrait, smallWidth }) => [
    portrait && !landscape && mobile,
    smallWidth
].some(Boolean);

const renderProductPart = (partType, data, key) => {
    const Component = components[partType];
    
    return <Component key={ key } { ...data } />;
}


export const Product = ({ path = '' }) => {
    const data = useInternalJson(path, {});
    const productClassName = getProductClassName(data);
    const responsiveInfo = useContextResponsive();
    const vertical = isContentVertical(responsiveInfo);
    const { content } = data;

    return (
        <section className={ productClassName }>
            { !!content && !!content.length && content.map(({ type, parts }, index) => (
                <div key={ index } className={ getContentClassName(type, vertical) }>
                    { !!parts && !!parts.length && parts.map((partType, i) => renderProductPart(partType, data, i))}
                </div>
            )) }          
        </section>
    );
};
