import React from 'react';
import './home-games.scss';
import { useInternalJson } from '../../utils/hooks/use-internal-json';
import { Product } from '../product/product';

export const HomeGames = () => {
    const { list } = useInternalJson('data/games/index.json', {});
    return (
        <div className="home-games-container">
            { !!list && list.map((path) => (
                <Product key={ path } path={ `data/games/${ path }` }/>
            )) }
        </div>
    )
}