import React from 'react';
import './home-about.scss';
import { useInternalJson } from '../../utils/hooks/use-internal-json';

export const HomeAbout = () => {
    const { content } = useInternalJson('data/about.json', {});
    const getMessages = (message) => message.split("\n").map((i, key) => <p key={ key }>{ i }</p>);
    
    return (
        <div className="home-about-container">
            { !!content && !!content.length && content.map(({ title, message }, key) => (
                <div key={ key } className="about-panel">
                    <h2>{ title }</h2>
                    { getMessages(message) }
                </div>
            )) }
        </div>
    )
};
